import { useState } from 'react'
import { Google } from '@mui/icons-material'

import { useNavigate } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import { Avatar, Typography, Button, Paper, Grid, Container } from '@mui/material'
import { LockOutlined } from '@mui/icons-material'
import { signIn, signUp } from '../../actions/auth'
import Input from './input'


import { signInWithGoogle } from './Firebase'
import useStyles from './styles'

const defForm = { firstname: '', lastname: '', email: '', password: '', confirmPassword: '' }

const Auth = () => {
    const navigate = useNavigate();
    const [showPass, setShowPass] = useState(false)
    const state = null
    const [isSignUp, setIsSignUp] = useState(false)
    const [formData, setFormData] = useState(defForm)
    const dispatch = useDispatch();

    const handleShowPassword = () => setShowPass(prevState => !prevState)

    const handleSubmit = (e) => {
        e.preventDefault();

        isSignUp ?
            dispatch(signUp(formData, navigate)) : dispatch(signIn(formData, navigate))
    }
    const handleChange = (e) => {
        setFormData({ ...formData, [e.target.name]: e.target.value })
    }

    const switchMode = (e) => {
        setIsSignUp(prevState => !prevState)
        setFormData(defForm)
        handleShowPassword()
    }

    const signWithGoogle = async (e) => {
        e.preventDefault()
        await signInWithGoogle()
            .then(res => googleSuccess(res))
            .catch(err => googleFailure(err))
    }

    const googleSuccess = (res) => {

        const result = res?.user;
        const token = res?.user?.accessToken;

        try {
            dispatch({ type: 'auth/AUTH', payload: { result, token } })

            navigate('/');
        } catch (err) {
            console.log(err)
        }
    }
    const googleFailure = (error) => {
        console.log(error)
        console.log('Google Sign In was unsucessful, Try Again')
    }

    return (
        <Container component='main' maxWidth='xs'>
            <Paper sx={useStyles.paper} elevation={3}>
                <Avatar sx={useStyles.avatar}>
                    <LockOutlined />
                </Avatar>
                <Typography variant='h5'>{isSignUp ? 'Sign Up' : 'Sign In'}</Typography>
                <form style={useStyles.form} onSubmit={handleSubmit}>
                    <Grid container spacing={2}>
                        {isSignUp && (
                            <>
                                <Input value={formData.firstname} name='firstname' label='First Name' handleChange={handleChange} autoFocus half />
                                <Input value={formData.lastname} name='lastname' label='Last Name' handleChange={handleChange} autoFocus half />
                            </>
                        )}

                        <Input value={formData.email} name='email' label='Email Address' handleChange={handleChange} type='email' />
                        <Input value={formData.password} name='password' label='password' handleChange={handleChange} type={showPass ? 'text' : 'password'} handleShowPassword={handleShowPassword} />
                        {isSignUp && <Input value={formData.confirmPassword} name='confirmPassword' label='Repeat Password' handleChange={handleChange} type='password' />}
                    </Grid>
                    <Button type='submit' fullWidth variant='contained' color='primary' sx={useStyles.submit}>
                        {isSignUp ? 'Sign Up' : 'Sign In'}
                    </Button>
                    <Button
                        sx={useStyles.googleButton}
                        color='primary'
                        fullWidth
                        variant='outlined'
                        onClick={signWithGoogle}
                        disabled={false}
                        startIcon={<Google />}>
                        {`Google ${isSignUp ? 'Sign Up' : 'Sign In'}`}
                    </Button>
                </form>
                {/* <Grid container justify='center'>
                        <Grid item> */}
                <Button fullWidth color='primary' onClick={switchMode} >
                    {
                        isSignUp ? 'Already have an account Sign In' : 'Dont have an account Sign Up'
                    }
                </Button>
                {/* </Grid>
                    </Grid> */}



            </Paper>
        </Container>
    )
}

export default Auth