import { AppBar, Typography, Toolbar, Button, Avatar } from '@mui/material'

import { useNavigate, useLocation } from 'react-router-dom'
import { useState, useEffect } from 'react'
import { useDispatch } from 'react-redux'
import { Link } from 'react-router-dom'

import decode from 'jwt-decode'

import { signOutGoogle } from '../Auth/Firebase'
import useStyles from './styles';
import memories from '../../images/memories.png'
import titleCase from '../../mods/toTitleCase'

const NavBar = () => {

    const location = useLocation();
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const user = JSON.parse(localStorage.getItem('profile'))


    const logout = () => {
        dispatch({ type: 'auth/LOGOUT' });
        signOutGoogle();
        navigate('/')
    }
    useEffect(() => {
        const token = user?.token;

        if (token) {
            const decodedToken = decode(token)

            if (decodedToken.exp * 1000 < new Date().getTime()) logout()
        }
    }, [location, user])


    return (
        <AppBar sx={useStyles.appBar} position="static" color="inherit">
            <div style={useStyles.brandContainer} >

                <Typography component={Link} to="/" sx={useStyles.title} variant="h4" align="center" >
                    Memories
                </Typography>
                <img style={useStyles.image} src={memories} alt="memories" height="40" />
            </div>
            <Toolbar sx={useStyles.toolbar}>
                {user ? (
                    <div style={useStyles.profile}>
                        <Avatar sx={useStyles.purple} alt={user?.result?.displayName ?? user?.result?.name} src={user?.result?.photoUrl}>
                            {user?.result?.displayName?.charAt(0) ?? user?.result?.name?.charAt(0)}
                        </Avatar>
                        <Typography sx={useStyles.username} variant='h6'>{user?.result?.displayName?.split(' ')[0] ?? user?.result?.name?.split(' ')[0]}</Typography>
                        <Button variant='contained' sx={useStyles.logout} color='secondary' onClick={logout} >Logout</Button>
                    </div>
                ) : (
                    <Button component={Link} to='/auth' variant='contained' color='primary'>SignIn</Button>
                )}
            </Toolbar>
        </AppBar>
    )
}


export default NavBar