import { useEffect } from 'react'

import { Paper, Typography, CircularProgress, Divider, Box } from '@mui/material'
import { useDispatch, useSelector } from 'react-redux'
import { useNavigate, useParams } from 'react-router-dom';
import { getPost } from '../../actions/posts';

import useStyles from './styles'
import moment from 'moment';

const PostDetails = () => {
    const { post, posts, isLoading } = useSelector((state) => state.posts)
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const { id } = useParams()
    console.log(useStyles)

    useEffect(() => {
        dispatch(getPost(id))
    }, [id])

    if (!post) return null



    return (
        isLoading ? (
            <Paper elevation={6} sx={useStyles.loadingPaper} >
                <CircularProgress size='7em' />
            </Paper >
        ) : (
            <Paper sx={{ padding: '20px', borderRadius: '15px' }} elevation={6}>
                <Box sx={useStyles.card}>
                    <Box sx={useStyles.section}>
                        <Typography variant='h4' component='h3'>{post.title}</Typography>
                        <Typography gutterBottom variant='h6' color='textSecondary' component='h2'>{post.tags.map((tag) => (`#${tag} `))}</Typography>
                        <Typography gutterBottom variant='body1' component='p'>{post.message}</Typography>
                        <Typography variant='h6'>Created by: {post.name}</Typography>
                        <Typography variant='body1'>{moment(post.createdAt).fromNow()}</Typography>
                        <Divider style={{ margin: '20px 0' }} />
                        <Typography variant='body1'><strong>Realtime Chat - coming soon</strong></Typography>
                        <Divider style={{ margin: '20px 0' }} />
                        <Typography variant='body1'><strong>Comments - coming soon</strong></Typography>
                        <Divider style={{ margin: '20px 0' }} />

                    </Box>
                    <Box sx={useStyles.imageSection}>
                        <img style={useStyles.media} src={post?.selectedFile || 'https://img.freepik.com/premium-vector/profile-icon-symbol-collection_663497-301.jpg?w=740'} alt={post.title} />
                    </Box>
                </Box>
            </Paper>
        )
    )
}

export default PostDetails