
const reducers = () => ({
    FETCH_ALL: (state, action) => (
        { ...state, posts: action.payload.data, currentPage: action.payload.currentPage, numberOfPages: action.payload.numberOfPages }
    ),
    FETCH_POST: (state, action) => (
        { ...state, post: action.payload }
    ),
    CREATE: (state, action) => (
        { ...state, posts: [action.payload, ...state.posts] }
    ),
    FETCH_BY_SEARCH: (state, action) => (
        { ...state, posts: action.payload }
    ),
    DELETE: (state, action) => (
        { ...state, posts: state.posts.filter((post) => post._id !== action.payload._id) }
    ),
    UPDATE: (state, action) => (
        { ...state, posts: state.posts.map((post) => action.payload._id == post._id ? action.payload : post) }
    ),
    StartLoading: (state, action) => (
        { ...state, isLoading: true }
    ),
    EndLoading: (state, action) => (
        { ...state, isLoading: false }
    )
})


export default reducers