import { useReducer, createContext, useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'

import { useLocation, useNavigate } from 'react-router-dom'
import ChipInput from 'material-ui-chip-input'
import { Container, Grow, Grid, Paper, AppBar, TextField, Button, Input } from '@mui/material'
import { getPosts, getPostsBySearch } from '../../actions/posts'
import Posts from '../Posts/Posts'
import Form from '../Form/Form'
import Pagination from '../pagination'
import useStyles from './styles'

const IdContext = createContext();

function useQuery() {
    return new URLSearchParams(useLocation().search)
}

const initState = null

const reducer = (state, action) => {
    switch (action.type) {
        case 'setCurrentId':
            return action.payload
        case 'reset':
            return initState
        default:
            return state;
    }
}
const Home = () => {
    const [currentId, dispatchId] = useReducer(reducer, initState);
    const [search, setSearch] = useState('');
    const [tags, setTags] = useState([]);

    const posts = useSelector((state) => state.posts);
    const query = useQuery();
    const navigate = useNavigate();
    const page = query.get('page') || 1;
    const searchQuery = query.get('searchQuery');


    const dispatch = useDispatch();
    const searchPost = () => {
        if (search.trim() || tags) {
            dispatch(getPostsBySearch({ search, tags: tags.join(',') }))
            navigate(`/posts/search?searchQuery=${search}&tags=${tags.join(',')}`)
        } else (
            navigate('/')
        )
    }

    const handleKeyPress = (e) => {
        if (e.keyCode == 13) {
            //serch Post
            searchPost();
        }
    }

    const handleAdd = (tag) => {
        setTags(prevTags => [...prevTags, tag])
    }
    const handleDelete = (tag) => setTags(tags.filter((_) => _ !== tag))


    return (
        <IdContext.Provider value={{ value: [currentId, dispatchId] }} >
            <Grow in>
                < Container maxWidth='xl' >
                    <Grid sx={useStyles.gridContainer} container justify="space-between" alignItems="stretch" spacing={7}>
                        <Grid item xs={12} sm={6} md={8}>
                            <Posts />
                        </Grid>
                        <Grid item xs={12} sm={6} md={4}  >
                            <AppBar sx={useStyles.appBarSearch} position='static' color='inherit'>
                                <TextField
                                    name='search'
                                    variant='outlined'
                                    label='Search Memories'
                                    onKeyPress={handleKeyPress}
                                    fullWidth
                                    value={search}
                                    onChange={(e) => { setSearch(e.target.value) }}
                                />
                                <ChipInput
                                    style={{ margin: '10px 0' }}
                                    value={tags}
                                    onAdd={handleAdd}
                                    onDelete={handleDelete}
                                    label='Search Tags'
                                    variant='outlined'
                                />
                                <Button variant='contained' onClick={searchPost} sx={useStyles.searchButton} color='primary'>Search</Button>
                            </AppBar>
                            <Form currentId={currentId} />
                            {(!searchQuery && !tags.length) && (

                                <Paper elevation={6}>
                                    <Pagination page={page} sx={useStyles.pagination} />


                                </Paper>
                            )}
                        </Grid>
                    </Grid>
                </Container >
            </Grow>
        </IdContext.Provider>
    )
}

export { IdContext }
export default Home

