import { createTheme } from '@mui/material/styles';

const defTheme = createTheme();

const theme = createTheme({
    recommendedPosts: {
        display: 'flex',
        [defTheme.breakpoints.between(0, 'sm')]: {
            flexDirection: 'column'
        }
    },

    imageSection: {
        position: 'relative',
        right: '20px',
        width: '40%',
        [defTheme.breakpoints.between(0, 990)]: {
            marginLeft: '20px',
            width: '90%'
        }
    },
    section: {
        borderRadius: '20px',
        margin: '10px',
        flex: 1
    },
    media: {
        borderRadius: '20px',
        objectFit: 'cover',
        maxWidth: '100%'
    },
    card: {
        display: 'flex',
        width: '100%',
        [defTheme.breakpoints.between(0, 990)]: {
            flexWrap: 'wrap',
            flexDirection: 'column'
        }
    },
    loadingPaper: {
        padding: '20px',
        borderRadius: '15px',
        display: 'flex',
        justifyContent: 'center'
    }

})

export default theme