import { useReducer, useContext, useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useNavigate } from 'react-router-dom'
import useStyles from './styles'
import FileBase from 'react-file-base64'
import { ThemeProvider } from '@mui/material/styles'
import { TextField, Button, Typography, Paper } from '@mui/material'
import { UserContext } from '../../App'
import { createPost, updatePost } from '../../actions/posts'
import { IdContext } from '../../components/Home/Home'

const initPost = {
    title: '', message: '', tags: '', selectedFile: ''
};

const postReducer = (state, action) => {
    switch (action.type) {
        case 'postData':
            return action.payload
        case 'setPostData':
            switch (action.name) {
                case 'tags':
                    return { ...state, 'tags': action.payload.split(',') }
            }
            return { ...state, [action.name]: action.payload }
        case 'resetPostData':
            return initPost
        default:
            return state
    }
}

const Form = () => {
    const [postData, dispatchPost] = useReducer(postReducer, initPost);
    const [currentId, dispatchId] = useContext(IdContext).value;

    const posts = useSelector((state) => state.posts);
    const post = useSelector((state) => currentId ? state.posts.posts.find((post) => post._id == currentId) : null);
    const user = JSON.parse(localStorage.getItem('profile'))
    const dispatch = useDispatch();
    const navigate = useNavigate();

    useEffect(() => {
        post ? dispatchPost({ type: 'postData', payload: post }) : dispatchPost({ type: 'resetPostData' })
    }, [post])


    useEffect(() => {

    }, [user])




    function handleSubmit(e) {
        e.preventDefault();
        currentId ? dispatch(updatePost(post._id, { ...postData, name: user?.result?.name ?? user?.result?.displayName })) : dispatch(createPost({ ...postData, name: user?.result?.name ?? user?.result?.displayName }, navigate));
        clear();
    }
    const clear = () => {
        dispatchPost({ type: 'resetPostData' })
        dispatchId({ type: 'reset' })
    }
    const handleChange = (e) => {
        const name = e.target.name
        // setPostData({ ...postData, [name]: e.target.value })
        dispatchPost({ type: 'setPostData', name, payload: e.target.value })
    }

    if (!user?.result) {
        return (
            <Paper elevation={6} sx={useStyles.paper}>
                <Typography variant='h6' align='center'>
                    Sign in to create a memory and like
                </Typography>
            </Paper>
        )
    }
    return (
        <Paper elevation={6} sx={useStyles.paper}>
            <form autoComplete="off" noValidate style={useStyles.form} onSubmit={(e) => handleSubmit(e)} >
                <Typography sx={useStyles.root} variant="h6">{currentId ? 'Editing' : 'Creating'} a memory</Typography>
                {/* <TextField sx={useStyles.root} name="creator" variant="outlined" label="Creator" fullWidth value={postData.creator} onChange={e => handleChange(e)} /> */}
                <TextField sx={useStyles.root} name="title" variant="outlined" label="Title" fullWidth value={postData.title} onChange={e => handleChange(e)} />
                <TextField sx={useStyles.root} name="message" variant="outlined" label="Message" fullWidth value={postData.message} onChange={e => handleChange(e)} />
                <TextField sx={useStyles.root} name="tags" variant="outlined" label="Tags" fullWidth value={postData.tags} onChange={e => handleChange(e)} />
                <div sx={useStyles.root} style={useStyles.fileInput}>
                    <FileBase tyle="file" multiple={false} onDone={({ base64 }) => dispatchPost({ type: 'setPostData', name: 'selectedFile', payload: base64 })} />
                </div>
                <Button sx={{ ...useStyles.buttonSubmit, ...useStyles.root }} variant="container" color="primary" size="large" type="submit" fullWidth>Submit</Button>
                <Button sx={useStyles.root} variant="contained" color="secondary" size="small" onClick={() => clear()} fullWidth>Clear</Button>
            </form>
        </Paper>
    );
}

export default Form;