const authReducers = () => ({
    AUTH: (state, action) => {
        localStorage.setItem('profile', JSON.stringify({ ...action?.payload }));
        return { ...state, authData: action.payload }

    },
    LOGOUT: (state, action) => {
        localStorage.clear();

        return { ...state, authData: null }
    }
})

export default authReducers