import axios from 'axios'


const dummyText = 'xxxjddndgdbxnskkskkskskdhfb'
const API = axios.create({ baseURL: 'https://tshabzz-symmetrical-winner-rxv97q6q4v73p6q7-8080.preview.app.github.dev' })
API.interceptors.request.use((req) => {

    const user = localStorage.getItem('profile')

    if (user) {
        req.headers.authorization = `Jonexxxxx ${JSON.parse(user).token}`
    }

    return req
});

export const fetchPosts = (page) => API.get(`/api?page=${page}`)
export const fetchPost = (id) => API.get(`/api/post/${id}`)
export const fetchPostsBySearch = (searchQuery) => API.get(`/api/search?searchQuery=${searchQuery.search || dummyText}&tags=${searchQuery.tags || dummyText}`)
export const createPost = (data) => API.post('/api/', data)
export const deletePost = (id) => API.delete(`/api/${id}`)
export const likePost = (id) => API.patch(`/api/like/${id}`)
export const updatePost = (id, post) => API.patch(`/api/${id}`, post)

export const signIn = (formData) => API.post('/api/signin', formData)
export const signUp = (formData) => API.post('/api/signup', formData)