import { useContext, useState, useEffect } from 'react'
import { useDispatch } from 'react-redux';

import { useNavigate } from 'react-router-dom'
import useStyles, { IconTheme } from './styles'
import { Card, CardActions, CardContent, CardMedia, ButtonBase, Button, Typography, IconButton } from '@mui/material';
import { Delete, ThumbUpAlt, MoreHoriz } from '@mui/icons-material'
import { deletePost, likePost } from '../../../actions/posts';
import { IdContext } from '../../../components/Home/Home'
import moment from 'moment';

const Post = ({ post }) => {
    const [, dispatchId] = useContext(IdContext).value;
    const user = JSON.parse(localStorage.getItem('profile'));
    const dispatch = useDispatch();
    const navigate = useNavigate();

    const openPost = () => navigate(`/posts/${post._id}`)

    return (
        <Card sx={useStyles.card} raised elevation={6}>
            <ButtonBase onClick={openPost}>
                <CardMedia component='img' sx={useStyles.media} image={post.selectedFile} alt={post.title} />
            </ButtonBase>
                <div style={useStyles.overlay}>
                    <Typography variant='h6'>{post.name}</Typography>
                    <Typography variant='body2'>{moment(post.createdAt).fromNow()}</Typography>
                </div>
                {(user?.result?._id == post.creator || user?.result?.uid == post.creator) && (
                    <div style={useStyles.overlay2}>
                        <IconButton size='small' onClick={() => { dispatchId({ type: 'setCurrentId', payload: post._id }) }}>
                            <MoreHoriz fontSize='inherit' />
                        </IconButton>
                    </div>
                )}
                <div style={{ ...useStyles.details, ...useStyles.title }}>
                    <Typography variant="body2" color="textSecondary">{post.tags.map((tag) => (`#${tag} `))}</Typography>
                </div>
                <CardContent>
                    <Typography sx={useStyles.title} variant="h5" gutterBottom>{post.title}</Typography>
                    <Typography sx={useStyles.title} variant="body2" gutterBottom>{post.message}</Typography>
                </CardContent>
                <CardActions>
                    <IconButton disabled={user ? false : true} sx={{}} size='medium' onClick={() => { dispatch(likePost(post._id)) }}>
                        <ThumbUpAlt fontSize='inherit' />
                    </IconButton>
                    <Typography>Like</Typography>
                    <Typography>{post.likes.length}</Typography>
                    {(user?.result?._id == post.creator || user?.result?.uid == post.creator) && (
                        <IconButton sx={{ size: 'small', color: 'primary' }} size='small' color='primary' onClick={() => { dispatch(deletePost(post._id)) }}>
                            <Delete fontSize='small' />
                        </IconButton>
                    )}
                </CardActions>
        </Card >
    );
}

export default Post;