import { createTheme } from '@mui/material/styles'

const defTheme = createTheme()

const theme = createTheme(({
    appBar: {
        height: '60px',
        borderRadius: 5,
        margin: '20px 0',
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'left',
        alignItems: 'center',
        padding: '10px 40px',
        [defTheme.breakpoints.between(0, 790)]: {
            flexDirection: 'column',
            height: '120px'
        }
    },
    heading: {
        color: 'rgba(0,183,255,1)',
        textDecoration: 'none'
    },
    image: {
        marginLeft: '15px'
    },
    title: {
        marginLeft: '15px',
        textDecoration: 'none',
        [defTheme.breakpoints.between(0, 900)]: {
            fontSize: '25px'
        }
    },
    toolbar: {
        display: 'flex',
        justifyContent: 'flex-end',
        width: '80%',
        [defTheme.breakpoints.between(0, 790)]: {
            justifyContent: 'center',
            width: '100%'
        }
    },
    profile: {
        display: 'flex',
        justifyContent: 'space-between',
        width: '50%',
        [defTheme.breakpoints.between(0, 760)]: {
            padding: '10px',
            width: '100%'
        },
        [defTheme.breakpoints.between(0, 950)]: {
            width: '80%'
        }
    },
    username: {
        display: 'flex',
        alignItems: 'center',
        justifySelf: 'center',
        fontSize: '18px',
        [defTheme.breakpoints.between(0, 790)]: {
            fontSize: '14px',
            margin: '5px'
        },
    },
    brandContainer: {
        display: 'flex',
        alignItems: 'center'
    },
    purple: {
        color: 'black',
        backgroundColor: 'rgba(117, 53, 185, 0.785)',
        [defTheme.breakpoints.between(0, 790)]: {
            height: '35px',
            width: '35px',
            
        }
    },
    logout: {
        [defTheme.breakpoints.between(0, 790)]: {
            height: '30px'
        },
        [defTheme.breakpoints.between(0, 850)]: {
            width: '50px',
            fontSize: '10px'
        }
    }

}));



export default theme