import * as api from '../api'
// import { FETCH_ALL, CREATE, DELETE, UPDATE } from '../features/postsSlice';

export const getPosts = (page) => async (dispatch) => {
    try {
        dispatch({ type: 'posts/StartLoading' })
        const { data } = await api.fetchPosts(page);
        console.log(data)

        dispatch({ type: 'posts/FETCH_ALL', payload: data })

        dispatch({ type: 'posts/EndLoading' })
    } catch (err) {
        console.log(err)
    }
}
export const getPost = (id) => async (dispatch) => {
    try {
        dispatch({ type: 'posts/StartLoading' })
        const { data } = await api.fetchPost(id);
        console.log(data)

        dispatch({ type: 'posts/FETCH_POST', payload: data })

        dispatch({ type: 'posts/EndLoading' })
    } catch (err) {
        console.log(err)
    }
}
export const getPostsBySearch = (searchQuery) => async (dispatch) => {
    try {
        dispatch({ type: 'posts/StartLoading' })
        const { data: { data } } = await api.fetchPostsBySearch(searchQuery);

        dispatch({ type: 'posts/FETCH_BY_SEARCH', payload: data })

        dispatch({ type: 'posts/EndLoading' })

    } catch (err) {
        console.log(err.message)
    }
}
export const createPost = (post, navigate) => async (dispatch) => {
    try {
        dispatch({ type: 'posts/StartLoading' })
        const { data } = await api.createPost(post);
        dispatch({ type: 'posts/CREATE', payload: data });

        dispatch({ type: 'posts/EndLoading' })

        navigate(`/posts/${data._id}`)
    } catch (err) {
        console.log(err.message)
    }
}

export const deletePost = (id) => async (dispatch) => {
    try {
        const { data } = await api.deletePost(id)
        dispatch({ type: 'posts/DELETE', payload: data })

    } catch (err) {
        console.log(err.message)
    }
}

export const likePost = (id) => async (dispatch) => {
    try {
        const { data } = await api.likePost(id)
        dispatch({ type: 'posts/UPDATE', payload: data })
    } catch (err) {
        console.log(err.message)
    }
}

export const updatePost = (id, post) => async (dispatch) => {
    try {
        const { data } = await api.updatePost(id, post)
        dispatch({ type: 'posts/UPDATE', payload: data })
    } catch (err) {
        console.log(err.message)
    }
}
