import { createTheme } from '@mui/material/styles';

const defTheme = createTheme();

const theme = createTheme({
    appBarSearch: {
        display: 'flex',
        borderRadius: 4,
        marginBottom: '1rem',
        padding: '16px'
    },
    pagination: {
        borderRadius: 4,
        marginTop: '1rem',
        padding: '16px'
    },
    gridContainer: {
        [defTheme.breakpoints.between(0, 'sm')]: {
            display: 'flex',
            flexDirection: 'column-reverse'
        }
    }
})

export default theme